/*
 *  Products carousel
 *    Styles for the products carousel component.
 *
 *  Version: 1.0.0
 *    1.0.0 - 2021/10/09
 */

.c-products-carousel {
  padding: 4.375rem 0;
  background-color: $s-color--background;

  // Title
  &__title {
    padding: 0 2.875rem;
    margin-bottom: 1rem;
    text-align: center;
  }

  // CTA
  &__cta {
    &-container {
      padding: 0 2.875rem;
      text-align: center;
      margin-bottom: 1.25rem;
    }
  }

  // Carousel
  &__carousel, &__carousel-container {
    position: relative;
  }

  &__carousel {
    display: flex;
    justify-content: center;
    overflow-x: auto;

    &.is-initialised {
      justify-content: initial;
      overflow: hidden;

      &.has-1-product {
        display: flex;
        justify-content: center;
        padding: 0 2.875rem;
      }
    }
  }

  &__slide {
    flex-shrink: 0;
    width: 100%;
  }

  &__navigation {
    display: none;
    position: absolute;
    top: calc(50% - 4.0313rem);
    left: 50%;
    transform: translate(-50%, -50%);
    justify-content: space-between;
    width: 100%;
    padding: 0 1.75rem;
    pointer-events: none;
    z-index: $s-z--text-overlay;

    .is-initialised + & {
      display: flex;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    pointer-events: initial;
    cursor: pointer;
    border-radius: 50%;
    background-color: rgba($s-color--background, 0.3);
  }

  &__icon {
    width: 0.5625rem;

    * {
      opacity: 1;
    }
  }

  /*** Media Queries ***/

  @media screen and (min-width: 34.375rem) {
    &__slide {
      width: 55.6%
    }
  }

  @include t-media-query($s-small-up) {
    &__slide {
      width: 33.33%;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 4.6875rem #{'min(6%, 7.5rem)'} 6.3rem;

    &__title {
      padding: 0;
    }

    &__cta-container {
      padding: 0;
      margin-bottom: 1.3rem;
    }

    &__slide {
      width: 25%;
      padding: 0 0.7031rem;
    }

    &__navigation {
      padding: 0;
      top: calc(50% - 3.0313rem);
      width: calc(100% + 5rem);
    }

    &__button {
      background-color: transparent;
      width: 2rem;
      height: 2rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 5rem #{'min(6%, 7.5rem)'} 12.6875rem;

    &__inner {
      max-width: 110rem;
      margin: 0 auto;
    }

    &__title {
      margin-bottom: 1.7188rem
    }

    &__cta-container {
      margin-bottom: 2.5938rem;
    }

    &__slide {
      padding: 0 1.4063rem;
    }

    &__icon {
      width: 0.8125rem;
      height: 1.35rem;
    }
  }
}
